import {
    REGISTER_SUCCESS,
    REGISTER_FAIL,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    LOGOUT_SUCCESS,
    LOGOUT_FAIL,
    AUTHENTICATED_SUCCESS,
    AUTHENTICATED_FAIL,
    DELETE_USER_SUCCESS,
    DELETE_USER_FAIL,
    CLEAR_LOGIN_ERRORS,
  } from '../actions/types';
  
  const initialState = {
    isAuthenticated: null,
    error: null, // Add error key to initial state
  };
  
  export default function authReducer(state = initialState, action) {
    const { type, payload } = action;
  
    switch (type) {
      case AUTHENTICATED_SUCCESS:
      case AUTHENTICATED_FAIL:
        return {
          ...state,
          isAuthenticated: payload,
          error: null, // Clear error when authentication status changes
        };
      case REGISTER_SUCCESS:
        return {
          ...state,
          isAuthenticated: false,
          error: null, // Clear error when registration succeeds
        };
      case LOGIN_SUCCESS:
        return {
          ...state,
          isAuthenticated: true,
          error: null, // Clear error when login succeeds
        };
      case LOGOUT_SUCCESS:
      case DELETE_USER_SUCCESS:
        return {
          ...state,
          isAuthenticated: false,
          error: null, // Clear error when logout or user deletion succeeds
        };
      case LOGIN_FAIL:
        return {
          ...state,
          error: payload, // Set error message on login failure
        };
      case CLEAR_LOGIN_ERRORS:
        return {
          ...state,
          error: null, // Clear login errors when requested
        };
      case REGISTER_FAIL:
      case LOGOUT_FAIL:
      case DELETE_USER_FAIL:
        return {
          ...state,
          error: null, // Clear error on other failures
        };
      default:
        return state;
    }
  }
  