// soundManager.js
import sellSound from './sounds/coinsSound.mp3';
import eatSound from './sounds/eatSound.mp3';
import equipSound from './sounds/equipSound.mp3';

class SoundManager {
  constructor() {
    this.sounds = {
      coins: new Audio(sellSound),
      eat: new Audio(eatSound),
      equip: new Audio(equipSound),
    };
  }

  playSound(name) {
    const audio = this.sounds[name];
    
    if (audio) {
      audio.pause();
      audio.currentTime = 0;
      audio.play();
    } else {
      console.warn(`Sound ${name} not found!`);
    }
  }
}

const soundManager = new SoundManager();
export default soundManager;
  