import React, { forwardRef } from 'react';
import { useLocation } from 'react-router-dom';
import { ALL_DISPLAY_ITEMS_TYPES, ALL_DISPLAY_STATS_NAMES } from '../Const';

const InventoryItemPopup = forwardRef(({ item, position }, ref) => {
  const location = useLocation();
  const isShopScreen = location.pathname === '/shop';

  const { base_stats, type, price } = item;

  const popupStyle = {
    backgroundColor: 'rgba(0, 0, 0, 0.9)',
    position: 'absolute',
    left: `${position.left}px`,
    top: `${position.top}px`,
    border: '3px solid rgb(240, 240, 240)',
    borderRadius: '5px',
    textAlign: 'center',
    zIndex: 9999,
  };

  const headerStyle = {
    backgroundColor: 'rgba(85, 85, 85, 0.5)',
    padding: '10px',
    borderBottom: '2px solid rgba(85, 85, 85, 1)',
    borderRadius: '2px',
  };

  const titleStyle = {
    margin: '0',
    fontSize: '25px',
  };

  const typeStyle = {
    color: 'rgba(170, 170, 170)',
    fontSize: '14px',
    margin: '0',
  };
  
  const textStyle = {
    margin: '0',
  };

  const coinStyle = {
    width: '20px',
    height: '20px',
  };

  const statNameStyle = {
    color: 'rgba(170, 170, 170)',
    fontSize: '18px',
  };

  const valueStyle = {
    color: 'white',
    fontSize: '18px',
  };
  // console.log(item);

  return (
    <div ref={ref} style={popupStyle} >
      <div style={headerStyle}>
        <h3 style={titleStyle}>{item.name}</h3>
      </div>

      <div style={{ padding: '8px' }}>
        <p style={typeStyle}>{ALL_DISPLAY_ITEMS_TYPES[type]}</p>

        {Object.entries(base_stats).map(([stat_name, stat_data]) => (
          <p key={stat_name} style={textStyle}>
            {stat_data["stat_type"] === "double_value" ? (
              <>
                <span style={statNameStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].label_before}</span>
                <span style={valueStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].format_first(stat_data.low_value)}</span>
                <span style={statNameStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].label_middle}</span>
                <span style={valueStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].format_second(stat_data.high_value)}</span>
                <span style={statNameStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].label_after}</span>
              </>
            ) : (
              <>
                <span style={statNameStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].label_before}</span>
                <span style={valueStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].format(stat_data.value)}</span>
                <span style={statNameStyle}>{ALL_DISPLAY_STATS_NAMES[stat_name].label_after}</span>
              </>
            )}
          </p>
        ))}

        
        {/* {isShopScreen && (
          <p style={{ color: 'white' }}>Dodatkowa zawarto dla ekranu walki</p>
        )} */}
      </div>

      <hr style={{ backgroundColor: 'white', height: '1px', border: 'none', margin: '0px 10px' }} />

      {isShopScreen && (
        <>
          <p style={{ color: 'white', margin: '2px 0', display: 'inline-block' }}>{price}</p>
          
          <img src={require('../Images/Shop/CoinSmall.png')} alt="Coin" style={{ width: '20px', height: '20px', verticalAlign: 'middle' }} />
        </>
      )}
    </div>
  );
});

export default InventoryItemPopup;
