import axios from 'axios';
import Cookies from 'js-cookie';

import {
    PLAYER_ITEM_SOLD_SUCCESS,
    PLAYER_ITEM_SOLD_FAIL,
    LOAD_PRODUCTS_DATA_SUCCESS,
    LOAD_PRODUCTS_DATA_FAIL,
    BUY_ITEM_SUCCESS,
    BUY_ITEM_FAIL
} from './types';

export const sellPlayerItem = (item_id) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };

    const body = JSON.stringify({ item_id });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/shop/sell_item`, body, config);

        if (res.data.player_inventory && res.data.player_currencies && res.data.gold_received) {
            dispatch({
                type: PLAYER_ITEM_SOLD_SUCCESS,
                payload: {
                    currencies: res.data.player_currencies,
                    player_inventory: res.data.player_inventory,
                    gold_received: res.data.gold_received
                }
            });
        } else {
            dispatch({
                type: PLAYER_ITEM_SOLD_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: PLAYER_ITEM_SOLD_FAIL
        });
    }
};

export const loadProductsData = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/shop/get`, config);

        if (res.data.products_data) {
            dispatch({
                type: LOAD_PRODUCTS_DATA_SUCCESS,
                payload: res.data.products_data
            });
        } else {
            dispatch({
                type: LOAD_PRODUCTS_DATA_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: LOAD_PRODUCTS_DATA_FAIL
        });
    }
};

export const buyItem = (item_name) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };

    const body = JSON.stringify({ item_name });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/shop/buy_item`, body, config);
        
        if (res.data.player_inventory && res.data.player_currencies && res.data.gold_spent) {
            dispatch({
                type: BUY_ITEM_SUCCESS,
                payload: {
                    currencies: res.data.player_currencies,
                    player_inventory: res.data.player_inventory,
                    gold_spent: res.data.gold_spent
                }
            });
        } else {
            dispatch({
                type: BUY_ITEM_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: BUY_ITEM_FAIL
        });
    }
};