import axios from 'axios';

import {
    LOAD_HERO_STATS_SUCCESS,
    LOAD_HERO_STATS_FAIL
} from './types';

export const load_stats = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/stats/get`, config);

        if (res.data.error) {
            dispatch({
                type: LOAD_HERO_STATS_FAIL
            });
        } else {
            dispatch({
                type: LOAD_HERO_STATS_SUCCESS,
                payload: res.data
            });
        }
    } catch (err) {
        dispatch({
            type: LOAD_HERO_STATS_FAIL
        });
    }
};

