import React, { useState, useEffect, useRef } from 'react';
import styles from './Item.module.css';
import InventoryItemPopup from './InventoryItemPopup';
import ConsumableItemPopup from './ConsumableItemPopup';
import { ALL_ITEMS_SIZES, ITEM_GRID_WIDTH, ITEM_GRID_HEIGHT } from '../Const';

const Item = ({ item, itemId, startPosLeft, startPosTop, listeners, equipState = false}) => {
    const [itemIsDragged, setItemIsDragged] = useState(false);
    const [draggedItemPosition, setDraggedItemPosition] = useState(false);
    const [popupPosition, setPopupPosition] = useState(false);
    
    const DRAGGED_ITEM_X_OFFSET = 25;
    const DRAGGED_ITEM_Y_OFFSET = 80;

    const NORMAL_ITEM_BACKGROUND = 'rgba(0, 0, 80, 0.2)';
    const NO_BACKGROUND          = 'rgba(0, 0, 0, 0)';  

    // INITIATION
    useEffect(() => {
        initImageSize();
        saveItemType();
      }, []); // Empty dependency array means this runs once on component mount
      
    const [itemImageSize, setItemImageSize] = useState({ x: 0, y: 0 });
    const initImageSize = () => {
        const { size } = item;
        const itemSize =  ALL_ITEMS_SIZES[size];

        const imageWidth  = ITEM_GRID_WIDTH  * itemSize[0];
        const imageHeight = ITEM_GRID_HEIGHT * itemSize[1];

        setItemImageSize({ x: imageWidth, y: imageHeight });
    };

    const [itemType, setItemType] = useState(false);
    const [isStackable, setIsStackable] = useState(false);
    const saveItemType = () => {
        const { type } = item;
        setItemType(type);

        if (type === 'consumable') {
            const { stackable } = item;
            
            setIsStackable(stackable);
        }
    };


    // Handle mouse over
    const [cursorOnItem, setCursorOnItem] = useState(false);

    const coursorEnterItem = () => {
        setCursorOnItem(true);
    };
    
    const coursorLeaveItem = () => {
        setCursorOnItem(false);
    };

    const itemPopupRef = useRef(null);
    useEffect(() => {
        if (cursorOnItem) {
            if (itemPopupRef.current !== null){
                const popupWidth = itemPopupRef.current.offsetWidth;

                setPopupPosition({ left: startPosLeft - popupWidth, top: startPosTop });
            }
        }
    }, [cursorOnItem]);

    // Handle dragging
    const itemDragStarted = (e) => {
        setDraggedItemPosition({ x: e.clientX - DRAGGED_ITEM_X_OFFSET, y: e.clientY - DRAGGED_ITEM_Y_OFFSET});
        setItemIsDragged(true);
        
        e.dataTransfer.setData('itemId', itemId);
    };

    useEffect(() => {
        const handleDocumentDragOver = (event) => {
            if (itemIsDragged) {
                setDraggedItemPosition({
                    x: event.clientX - DRAGGED_ITEM_X_OFFSET,
                    y: event.clientY - DRAGGED_ITEM_Y_OFFSET
                });
            }
        };

        document.addEventListener('dragover', handleDocumentDragOver);

        return () => {
            document.removeEventListener('dragover', handleDocumentDragOver);
        };
    }, [itemIsDragged]);

    const handleDragEnd = (e) => {
        if(equipState){
            setItemIsDragged(false); 
            setIsFirstDrag(true);
        }
    };

    const [isFirstDrag, setIsFirstDrag] = useState(true);

    const posLeftRef = useRef(startPosLeft);
    const numberOfDragRendersRef = useRef(0);

    const createStyleItem = () => {
        posLeftRef.current = startPosLeft
        if (itemIsDragged && isFirstDrag) {
            numberOfDragRendersRef.current = 0;
            setIsFirstDrag(false);
        } else if (itemIsDragged && !isFirstDrag) {
            if (numberOfDragRendersRef.current > 3){ 
                posLeftRef.current = 0;
            } else{
                numberOfDragRendersRef.current += 1;
            }
        }

        let newOpacity = 1
        if (itemIsDragged){
            newOpacity = 0;
        }

        const itemImageStyle = {
            width:  `${itemImageSize.x}px`,
            height: `${itemImageSize.y}px`,

            position: 'absolute',

            left: `${posLeftRef.current}px`,
            top:  `${startPosTop}px`,

            backgroundColor: itemIsDragged ? NO_BACKGROUND : NORMAL_ITEM_BACKGROUND,
            opacity: newOpacity,
        };

        return itemImageStyle
    };

    const createStyleItemDraggedImage = () => {
        const itemDraggedImageStyle = {
            width:  `${itemImageSize.x}px`,
            height: `${itemImageSize.y}px`,
    
            position: 'absolute',

            left: `${draggedItemPosition.x}px`,
            top: `${draggedItemPosition.y}px`,
    
            pointerEvents: 'none',
        };

        return itemDraggedImageStyle
    };


    // Function to register custom event listeners
    const registerListeners = () => {
        // Define a mapping of event names to their corresponding functions
        const eventMappings = {
            onDragStart: itemDragStarted,
            onDragEnd: handleDragEnd,
            onMouseEnter: coursorEnterItem,
            onMouseLeave: coursorLeaveItem,
        };

        // Loop through the event names and check if additional functions are provided
        Object.keys(eventMappings).forEach((eventName) => {
            const originalFunction = eventMappings[eventName];
            const additionalFunction = listeners[eventName];

            if (typeof additionalFunction === 'function') {
                // If additional function is provided, create a wrapper function that calls both
                eventMappings[eventName] = (e) => {
                    originalFunction(e);
                    additionalFunction(e, itemId);
                };
            }
        });

        // If even function is given only by listner but not original object, create a wrapper function
        for (const eventName in listeners) {
            if (typeof listeners[eventName] === 'function' && !eventMappings[eventName]) {
                eventMappings[eventName] = (e) => {
                    listeners[eventName](e, itemId);
                };
            }
        }

        return eventMappings;
    };

    const eventListeners = registerListeners();

    return (
        <>
            <div
                style={createStyleItem()}
                draggable="true"

                {...eventListeners}
            >
                <img
                    className={`${styles.itemIcon} ${styles.itemIconStatic}`}
                    draggable="false"
                    src={require(`../` + item.image)}
                    alt={'item_image'}
                />
                {isStackable && (
                    <div className={styles.itemQuantity}>
                        {item.number}
                    </div>
                )}
            </div>

            {cursorOnItem && !itemIsDragged && (
                <>
                    {itemType === 'consumable' ? (
                        <ConsumableItemPopup
                            item={item}
                            position={popupPosition}
                            ref={itemPopupRef}
                            style={{ pointerEvents: 'none' }}
                        />
                    ) : (
                        <InventoryItemPopup
                            item={item}
                            position={popupPosition}
                            ref={itemPopupRef}
                            style={{ pointerEvents: 'none' }}
                        />
                    )}
                </>
            )}

            {itemIsDragged ? (
                <div
                    key={`dragged_item_image`}
                    style={createStyleItemDraggedImage()}
                    draggable="true"
                >
                    <img
                        src={require(`../` + item.image)}
                        alt="Dragged item"
                        className={`${styles.itemIcon} ${styles.itemIconDragged}`}
                    />
                </div>
            ) : null}
        </>

    );
};

export default Item;