import axios from 'axios';
import Cookies from 'js-cookie';

import { 
    FIGHT_START_SUCCESS, 
    FIGHT_START_FAILURE,

    FIGHT_STATUS_TRUE,
    FIGHT_STATUS_FALSE,
    FIGHT_STATUS_FAILURE,

    RETREAT_STATUS_TRUE,
    RETREAT_STATUS_FALSE,

    LEAVE_STATUS_TRUE,
    LEAVE_STATUS_FALSE,

    GET_HUNT_LEVELS_SUCCESS,
    GET_HUNT_LEVELS_FAILURE,
} from './types';


export const start_fight = (monster_lvl) => async (dispatch) => {
    const config = {
        headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'X-CSRFToken': Cookies.get('csrftoken'),
        },
    };
    
    const body = JSON.stringify({ monster_lvl });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/fight/start_fight`, body, config);
    
        if (res.data.fightStarted === true) {
            dispatch({
                type: FIGHT_START_SUCCESS,
                payload: res.data, // Assuming the response contains the necessary data
            });
        } else {
            dispatch({
                type: FIGHT_START_FAILURE,
                payload: res.data.message
            });
        }
    } catch (error) {
        dispatch({
            type: FIGHT_START_FAILURE,
            payload: error.message
        });
    }
};

export const retreat_fight = (didRetreat) => async (dispatch) => {
    if (didRetreat) {
        dispatch({
            type: RETREAT_STATUS_TRUE,
            payload: "Player retreat from fight!",
        });
    } else {
        dispatch({
            type: RETREAT_STATUS_FALSE,
            payload: "Player couldn't retreat from fight!",
        });
    }
};

export const leave_fight = (didRetreat) => async (dispatch) => {
    if (didRetreat) {
        dispatch({
            type: LEAVE_STATUS_TRUE,
            payload: "Player left fight!",
        });
    } else {
        dispatch({
            type: LEAVE_STATUS_FALSE,
            payload: "Player couldn't leave fight!",
        });
    }
};

export const get_fight_status = () => async (dispatch) => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        },
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/fight/fight_status`, config);

        if (res.data.inFight !== undefined) {
            if (res.data.inFight) {
                dispatch({
                    type: FIGHT_STATUS_TRUE,
                    payload: res.data,
                });
            } else {
                dispatch({
                    type: FIGHT_STATUS_FALSE,
                    payload: res.data,
                });
            }
        } else {
            dispatch({
                type: FIGHT_STATUS_FAILURE,
                payload: 'Invalid response data',
            });
        }
    } catch (error) {
        dispatch({
            type: FIGHT_STATUS_FAILURE,
            payload: error.message,
        });
    }
};

export const get_hunt_levels_unlocked = () => async (dispatch) => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        },
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/fight/hunt_level_unlocked/`, config);

        if (res.data.hunt_levels_unlocked !== undefined) {
            dispatch({
                type: GET_HUNT_LEVELS_SUCCESS,
                payload: res.data.hunt_levels_unlocked,
            });
        } else {
            dispatch({
                type: GET_HUNT_LEVELS_FAILURE,
                payload: 'Invalid response data',
            });
        }
    } catch (error) {
        dispatch({
            type: GET_HUNT_LEVELS_FAILURE,
            payload: error.message,
        });
    }
};