import React, { Fragment } from "react";
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import styles from '../containersCSS/Home.module.css'; // Import the CSS file

const Home = ({ isAuthenticated }) => {
    const buttons = isAuthenticated ? (
        <Link to='/dashboard' className={`btn btn-primary btn-lg ${styles.customButton}`}> 
            Go to Dashboard
        </Link>
    ) : (
        <Fragment>
            <Link to='/register' className={`btn btn-primary btn-lg ${styles.customButton}`}> 
                Register
            </Link>
            <Link to='/login' className={`btn btn-primary btn-lg ${styles.customButton}`}> 
                Login
            </Link>
        </Fragment>
    );

    return (
        <div className={`${styles.containerBackground}`}>        
            <div className={`${styles.container} mt-5 p-4`}>
                <h1 className='display-4'>Welcome to Browser Battle Challenge</h1>

                <p className={`${styles.lead} mt-3`}>
                    Embark on an epic quest within the mystical realms of Browser Battle Challenge. <br />
                    Sharpen your sword, harness your magic, and summon your allies as you delve into a world of knights, dragons, and sorcery. <br />
                    Conquer medieval battlegrounds, challenge other heroes, and become the ultimate champion in this legendary browser adventure! <br />
                </p>

                <div className='button-container mt-3'>
                    {buttons}
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps)(Home);
