import {
    PLAYER_ITEM_SOLD_SUCCESS,
    LOAD_PRODUCTS_DATA_SUCCESS,
    LOAD_PRODUCTS_DATA_FAIL,
    BUY_ITEM_SUCCESS,
} from '../actions/types';

const initialState = {
    gold_received: 0,
    gold_spent: 0,
    products_data: []
};

export default function shopReducer(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case PLAYER_ITEM_SOLD_SUCCESS:
            return {
                ...state,
                gold_received: payload.gold_received
            };
        case LOAD_PRODUCTS_DATA_SUCCESS:
            return {
                ...state,
                products_data: payload
            };
        case LOAD_PRODUCTS_DATA_FAIL:
            return {
                ...state,
                products_data: []
            };
        case BUY_ITEM_SUCCESS:
            return {
                ...state,
                gold_spent: payload.gold_spent
            };
        default:
            return state;
    }
}
