import {
    LOAD_HERO_CURRENCIES_SUCCESS,
    LOAD_HERO_CURRENCIES_FAIL,
    PLAYER_ITEM_SOLD_SUCCESS,
    BUY_ITEM_SUCCESS,
} from '../actions/types';

const initialState = {
    gold: 0,
    diamonds: 0,
};

export function unpack_currencies(payload) {
    return {
        gold: payload.gold,
        diamonds: payload.diamonds,
    };
}

export default function currenciesReducer(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case LOAD_HERO_CURRENCIES_FAIL:
            return initialState;
        case LOAD_HERO_CURRENCIES_SUCCESS:
            return {
                ...state,
                ...unpack_currencies(payload),
            };
        case PLAYER_ITEM_SOLD_SUCCESS:
        case BUY_ITEM_SUCCESS:
            return {
                ...state,
                ...unpack_currencies(payload.currencies),
            }
        default:
            return state;
    }
}
