import{
    LOAD_HERO_STATS_SUCCESS,
    LOAD_HERO_STATS_FAIL
} from '../actions/types';

const initialState = {
    min_dmg: 10,         // Renamed "damage" to "min_damage"
    max_dmg: 10,         // Added "max_damage"
    current_health: 100,    // Renamed "health" to "current_health"
    max_health: 100,        // Added "max_health"
    armor: 10,
  };
export default function statsReducer(state = initialState, action){
    const {type, payload} = action;
    
    switch(type){

        case LOAD_HERO_STATS_FAIL:
            return initialState;
        case LOAD_HERO_STATS_SUCCESS:
            return {
                ...state,
                ...payload.hero_stats,
            }
        default:
            return state
    }
};