import { useRef, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import styles from '../containersCSS/Fight.module.css'; // Import the CSS file
import { retreat_fight, leave_fight } from '../../actions/fight';
import { handleWebSocketMessage } from './FightWebSocketLogic';

import TransitionScreen from './TransitionScreen';

const MonstersFightPage = ({ retreat_fight, leave_fight, fightData }) => {
    const [messages, setMessages] = useState([]);
    const messagesEndRef = useRef(null);

    const [fightIsOver, setFightIsOver] = useState(false);

    const [heroName, setHeroName] = useState("User Name");
    const [heroMinDamage, setHeroMinDamage] = useState("0");
    const [heroMaxDamage, setHeroMaxDamage] = useState("0");
    const [heroCurrentHealth, setHeroCurrentHealth] = useState("0");
    const [heroMaxHealth, setHeroMaxHealth] = useState("0");
    const [heroArmor, setHeroArmor] = useState("0");
    const [heroSpeed, setHeroSpeed] = useState("0");

    const [monsterName, setMonsterName] = useState("Monster Name");
    const [monsterMinDamage, setMonsterMinDamage] = useState("0");
    const [monsterMaxDamage, setMonsterMaxDamage] = useState("0");
    const [monsterMaxHealth, setMonsterMaxHealth] = useState("0");
    const [monsterCurrentHealth, setMonsterCurrentHealth] = useState("0");
    const [monsterArmor, setMonsterArmor] = useState("0");
    const [monsterSpeed, setMonsterSpeed] = useState("0");
    const [monsterImagePath, setMonsterImagePath] = useState("Images/Enemies/Rabbit.png");

    useEffect(() => {
        if (fightData) {
            setFightIsOver(fightData.fightIsOver || false);

            setHeroName(fightData.heroStats.hero_name || "User Name");
            setHeroMinDamage(fightData.heroStats.hero_min_damage || "0");
            setHeroMaxDamage(fightData.heroStats.hero_max_damage || "0");
            setHeroCurrentHealth(fightData.heroStats.hero_current_health || "0");
            setHeroMaxHealth(fightData.heroStats.hero_max_health || "0");
            setHeroArmor(fightData.heroStats.hero_armor || "0");
            setHeroSpeed(fightData.heroStats.hero_speed || "0");

            setMonsterName(fightData.monsterStats.monster_name || "0");
            setMonsterMinDamage(fightData.monsterStats.monster_min_damage || "0");
            setMonsterMaxDamage(fightData.monsterStats.monster_max_damage || "0");
            setMonsterMaxHealth(fightData.monsterStats.monster_max_health || "0");
            setMonsterCurrentHealth(fightData.monsterStats.monster_current_health || "0");
            setMonsterArmor(fightData.monsterStats.monster_armor || "0");
            setMonsterSpeed(fightData.monsterStats.monster_speed || "0");
            setMonsterImagePath(fightData.monsterStats.monster_image_path || "Images/Enemies/Rabbit.png");
        }
    }, [fightData]);

    const updateFightData = (setters) => {
        for (const setterName in setters) {
            try {
                if (typeof eval(setterName) === 'function') {
                    eval(setterName)(setters[setterName]);
                }
            } catch (error) {}
        }
    };

    // const [currentSocket, setCurrentSocket] = useState(null)
    const currentSocket = useRef(null);
    const isSocketOpen = useRef(false);
    const isInitialMessageSent = useRef(false); // Add this ref to track if the initial message was sent
    
    useEffect(() => {
        const wsProtocol = window.location.protocol === 'https:' ? 'wss' : 'ws';
        const socket = new WebSocket(`${wsProtocol}://${window.location.host}/ws/socket-server/`);
    
        socket.onmessage = function (e) {
            const data = JSON.parse(e.data);
            // console.log("INCOMING MESSAGE: " + JSON.stringify(data));
            
            if ('action' in data){
                handleWebSocketMessage(data, setMessages, updateFightData);

                if (data.action === 'fight_over_retreat'){ 
                    retreat_fight(true);
                }
        
                if (data.action === 'fight_leave'){ 
                    leave_fight(true);
                }
            }
        };
    
        socket.addEventListener('open', () => {
            isSocketOpen.current = true;
            currentSocket.current = socket;

            if (!isInitialMessageSent.current) {
                sendWebSocketMessage({
                    action: 'initialDataLoad',
                    type: 'userBattleAction'
                });

                isInitialMessageSent.current = true;
            }
        });
    
        socket.addEventListener('close', () => {
            isSocketOpen.current = false;
        });
    
        return () => { socket.close(); };
    }, []);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    };

    useEffect(() => {
        scrollToBottom();
    }, [messages]);

    
    const sendWebSocketMessage = (messageData) => {
        if (isSocketOpen.current) {
            currentSocket.current.send(JSON.stringify({
                'messageData': messageData
            }));
        }
    };

    return (
        <div>
            <div className={`${styles.containerBackground}`}>
                <TransitionScreen/>
                <div className={`${styles.fightContainer}`}>
                    <div className={styles.heroMonsterContainer}>
                        <div className={styles.heroContainer}>
                            <div className={styles.heroHeader}>
                                {heroName}
                            </div>

                            <img src={require('../../Images/HeroScreen/Hero.png')} alt="Hero" className={styles.heroImage} />

                            <div className={`${styles.statsCanvas}`}>
                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Damage:</span>
                                    <span className={`${styles.statValue}`}>{heroMinDamage} - {heroMaxDamage}</span>
                                </div>

                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Health:</span>
                                    <span className={`${styles.statValue}`}>{heroCurrentHealth} / {heroMaxHealth}</span>
                                </div>

                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Armor:</span>
                                    <span className={`${styles.statValue}`}>{heroArmor}</span>
                                </div>

                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Speed:</span>
                                    <span className={`${styles.statValue}`}>{heroSpeed}</span>
                                </div>
                            </div>
                        </div>

                        <div className={styles.monsterContainer}>
                            <div className={styles.monsterHeader}>
                                {monsterName}
                            </div>               

                            <img src={require(`../../${monsterImagePath}`)} alt="Enemy" className={styles.monsterImage}/>
                            
                            <div className={`${styles.statsCanvas}`}>
                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Damage:</span>
                                    <span className={`${styles.statValue}`}>{monsterMinDamage} - {monsterMaxDamage}</span>
                                </div>
                                
                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Health:</span>
                                    <span className={`${styles.statValue}`}>{monsterCurrentHealth} / {monsterMaxHealth}</span>
                                </div>

                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Armor:</span>
                                    <span className={`${styles.statValue}`}>{monsterArmor}</span>
                                </div>

                                <div className={`${styles.stat}`}>
                                    <span className={`${styles.statName}`}>Speed:</span>
                                    <span className={`${styles.statValue}`}>{monsterSpeed}</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={styles.battleActionsCanvas}>
                        {fightIsOver ? (
                            <button className={`${styles.button} ${styles.leaveButton}`} 
                                onClick={() => 
                                    sendWebSocketMessage({
                                        action: 'leave',
                                        type: 'userBattleAction' })}
                            >Leave fight
                            </button>
                        ) : (
                            <button className={`${styles.button} ${styles.retreatButton}`} 
                                onClick={() => sendWebSocketMessage({
                                    action: 'retreat',
                                    type: 'userBattleAction' })}>Retreat
                            </button>
                        )}

                        {!fightIsOver && (
                            <button className={`${styles.button} ${styles.autoButton}`}  
                                onClick={() => sendWebSocketMessage({
                                    action: 'auto',
                                    type: 'userBattleAction'
                                })}>Auto
                            </button>
                        )}

                        {!fightIsOver && (
                            <button className={`${styles.button} ${styles.attackButton}`}  
                                onClick={() => sendWebSocketMessage({
                                    action: 'attack',
                                    type: 'userBattleAction'
                                })}>Attack!
                            </button>
                        )}
                    </div>
                </div>

                <div className={styles.fightLog}>
                    {messages.map((message, index) => (
                        <div key={index} className={`${styles.messageContainer} ${styles[message.className]}`}>
                            <div
                                className={`${styles.message}`}
                                dangerouslySetInnerHTML={{ __html: message.content }}
                            />
                        </div>
                    ))}
                    <div ref={messagesEndRef} />
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    fightData: state.fight.fightData, // Get fightData from the state
});

export default connect(mapStateToProps, { retreat_fight, leave_fight })(MonstersFightPage);