import {
    LOAD_PLAYER_INVENTORY_SUCCESS,
    LOAD_PLAYER_INVENTORY_FAIL,
    MOVE_ITEM_SUCCESS,
    MOVE_ITEM_FAIL,
    USE_ITEM_SUCCESS,
    USE_ITEM_FAIL,
    USE_CONSUMABLE_ITEM_SUCCESS,
    PLAYER_ITEM_SOLD_SUCCESS,
    BUY_ITEM_SUCCESS,
} from '../actions/types';

const initialState = {
    playerInventory: {},
};

export function buildInventoryData(payload) {
    return {
        items: payload.player_inventory.items,
        inventory_grid: payload.player_inventory.inventory_grid,
        equipped: {
            weapon_left : payload.player_inventory.weapon_left,
            weapon_right: payload.player_inventory.weapon_right,
            body_armor  : payload.player_inventory.body_armor,
            gloves      : payload.player_inventory.gloves,
            boots       : payload.player_inventory.boots,
            helmet      : payload.player_inventory.helmet,
            belt        : payload.player_inventory.belt,
            amulet      : payload.player_inventory.amulet,
            ring_left   : payload.player_inventory.ring_left,
            ring_right  : payload.player_inventory.ring_right,
        },
    };
}

export default function inventoryReducer(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case LOAD_PLAYER_INVENTORY_FAIL:
            return {
                ...state,
            };
        case LOAD_PLAYER_INVENTORY_SUCCESS:
            return {
                ...state,
                ...buildInventoryData(payload),
            };
        case MOVE_ITEM_SUCCESS:
            return {
                ...state,
                ...buildInventoryData(payload),
            };
        case MOVE_ITEM_FAIL:
            return {
                ...state,
                error: 'Item move failed',
            };
        case USE_ITEM_SUCCESS:
            return {
                ...state,
                ...buildInventoryData(payload),
            };
        case USE_ITEM_FAIL:
            return {
                ...state,
                error: 'Item use failed', 
            };
        case USE_CONSUMABLE_ITEM_SUCCESS:
            return {
                ...state,
            }
        case PLAYER_ITEM_SOLD_SUCCESS:
        case BUY_ITEM_SUCCESS:
            return {
                ...state,
                ...buildInventoryData(payload),
            };
        default:
            return state;
    }
}

