import React, { Fragment } from "react";
import { Link, NavLink, useLocation } from 'react-router-dom';
import { connect } from "react-redux";
import { logout } from "../actions/auth";
import './Navbar.css'; // Import your custom CSS file

const Navbar = ({ isAuthenticated, logout}) => {
    const location = useLocation(); // Use the useLocation hook

    const isFightScreen = location.pathname === '/fight';

    const authLinks = (
        <Fragment>
            <ul className="navbar-nav">
                <li className='nav-item'>
                    <NavLink className='nav-link btn btn-light' to='/hero'>Hero</NavLink>
                </li>
            </ul>

            <ul className="navbar-nav">
                <li className='nav-item'>
                    <NavLink className='nav-link btn btn-light' to='/shop'>Shop</NavLink>
                </li>
            </ul>
            
            <ul className="navbar-nav">
                <li className='nav-item'>
                    <NavLink className='nav-link btn btn-light' to='/monsters_hunt'>Monster hunt</NavLink>
                </li>
            </ul>

            <ul className="navbar-nav">
                <li className='nav-item'>
                    <NavLink className='nav-link btn btn-light' to='/dashboard'>Dashboard</NavLink>
                </li>
            </ul>

            <ul className="navbar-nav ms-auto">
                <li className='nav-item'>
                    <a className='nav-link btn btn-danger btn-visible' onClick={logout} href='#!'>Logout</a>
                </li>
            </ul>
        </Fragment>
    );

    const guestLinks = (
        <Fragment>
            <ul className="navbar-nav">
                <li className="nav-item">
                    <NavLink className="nav-link btn btn-light" to="/login">Login</NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link btn btn-light" to="/register">Register</NavLink>
                </li>
            </ul>
        </Fragment>
    );

    return (
        !isFightScreen && (
            <nav className="navbar navbar-expand-lg bg-body-tertiary">
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">Browser Battle Challenge</Link>

                    <button className="navbar-toggler" 
                            type="button" data-bs-toggle="collapse" 
                            data-bs-target="#navbarNav" 
                            aria-controls="navbarNav" 
                            aria-expanded="false" 
                            aria-label="Toggle navigation"
                    >
                        <span className="navbar-toggler-icon"></span>
                    </button>

                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <NavLink className="nav-link btn btn-light" to="/">Home</NavLink>
                            </li>
                        </ul>
                        
                        {/* {isAuthenticated ? authLinks : authLinks} */}
                        {isAuthenticated ? authLinks : guestLinks}
                    </div>
                </div>
            </nav>
        )
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, {logout})(Navbar);
