import React, { useState, useEffect } from 'react';
import '../containersCSS/TransitionScreen.scss'; // Import the CSS file

const TransitionScreen = () => {
    const [isOverlayVisible, setIsOverlayVisible] = useState(true);

    // Use useEffect to automatically hide stripes after a delay
    useEffect(() => {
        const hideStripesTimeout = setTimeout(() => {
            setIsOverlayVisible(false);
        }, 1000); // Adjust the delay (in milliseconds) as needed

        return () => {
            clearTimeout(hideStripesTimeout);
        };
    }, []);

    return (
        <div className="transition-container">
            <div className={`overlay ${isOverlayVisible ? 'show' : ''}`}>
                <div key={1} className="bar stripe1"></div>
                <div key={2} className="bar stripe2"></div>
                <div key={3} className="bar stripe3"></div>
                <div key={4} className="bar stripe4"></div>
            </div>
        </div>
    );
};

export default TransitionScreen;
