import React, { useEffect, useRef } from 'react';
import backgroundMusic from './Fantasy RPG Music Pack/mp3/Ambient 3.mp3'; // Import your background music file

const BackgroundMusic = () => {
  const audioRef = useRef(new Audio(backgroundMusic));

  useEffect(() => {
    const audio = audioRef.current;
    audio.loop = true;

    // Function to play audio
    const playAudio = () => {
      audio.play().catch(error => {
        console.error('Error playing background music:', error);
      });
    };

    // Check if user has interacted with the page
    const handleUserInteraction = () => {
      playAudio();
      window.removeEventListener('click', handleUserInteraction); // Remove the event listener after first interaction
    };

    // Add event listener to play audio after user interaction
    window.addEventListener('click', handleUserInteraction);

    return () => {
      audio.pause();
      window.removeEventListener('click', handleUserInteraction); // Clean up the event listener
    };
  }, []);

  return null;
};

export default BackgroundMusic;