function retrieveSettersFromData(data) {
    const setters = {};

    for (const key in data.update) {
        let  combatantName = "";

        if (key.endsWith('_1')) {
            combatantName = "Hero";
        } else if (key.endsWith('_2')) {
            combatantName = "Monster";
        } else if (key.endsWith('_3')) {
            combatantName = "";
        }

        const statName = key.match(/^(.*?)_\d$/)[1]; // Remove _number from string
        const statNameNoGaps = statName.split('_');
        
        // Convert each part to title case (capitalize the first letter)
        const statNameNoGapsCase = statNameNoGaps.map((part) =>
            part.charAt(0).toUpperCase() + part.slice(1)
        );
        
        const statNameCase = statNameNoGapsCase.join(''); // Join the title case parts together to form the desired string
        
        const setterName = "set" + combatantName + statNameCase;

        setters[setterName] = data.update[key];
    }
  
    return setters;
}

function handleWebSocketMessage(data, setMessages, updateFightData) {
    // console.log('Battle data received:', data);

    if (Array.isArray(data['messages'])) {
        data['messages'].forEach((message) => {
            // console.log('MY MESSAGE: ', message);
            if ('message_type' in message && message.message_type.includes('sbm')) {
                const newMessage = message.message.replace(/\n/g, "<br />");
                const messageClass = message.message_type.slice(4);

                // console.log('Battle data received 222:', messageClass);
                setMessages((prevMessages) => [
                    ...prevMessages,
                    {
                        content: newMessage,
                        className: 'messageContainer_' + messageClass,
                    },
                ]);
                // setMessages((prevMessages) => [...prevMessages, newMessage]);
            }
        });
    }

    if (data.update) {
        const setters = retrieveSettersFromData(data)

        updateFightData(setters)
    }
}

export { handleWebSocketMessage };