import React, { useEffect, useState, useRef, useMemo } from 'react';
import styles from '../containersCSS/Shop.module.css';
import { connect } from 'react-redux';
import { loadCurrencies } from '../../actions/currencies';
import { loadPlayerInventory, moveItemInInventory, inventoryItemUse, resetItemConsumed } from '../../actions/inventory';
import { sellPlayerItem, loadProductsData, buyItem } from '../../actions/shop';
import { ITEM_GRID_WIDTH, ITEM_GRID_HEIGHT, BAG_WIDTH, BAG_HEIGHT, ALL_ITEMS_SIZES } from '../../Const';
import Item from '../../components/Item';
import ItemShop from '../../components/ItemShop';
import GridSquare from '../../components/GridSquare';

const Shop = ({ 
  nick_name_global, currencies, items, inventory_grid, equipped, 
  loadCurrencies, loadPlayerInventory, moveItemInInventory, 
  inventoryItemUse, sellPlayerItem, loadProductsData, buyItem, shopState, shopProducts
}) => {
  const [bagItems, setBagItems] = useState(null);
  const [equippedItems, setEquippedItems] = useState(null);

  const gridContainerRef = useRef(null);
  const [isCoursorOnBag, setIsCursorOnBag] = useState(null);
  
  const [draggedItemId, setDraggedItemId]             = useState(0);
  const [draggedItemPosition, setDraggedItemPosition] = useState({ x: 0, y: 0 });

  const [canBeDroppedOnBag, setCanBeDroppedOnBag] = useState(false);
  const [currentOccludedSquare, setcurrentOccludedSquare] = useState({ x: 0, y: 0 });

  const [showSellAnimation, setShowSellAnimation] = useState(false);
  const [animationPosition, setAnimationPosition] = useState({ x: 0, y: 0 });
  const [animationKey, setAnimationKey] = useState(0);
  const [animationGoldValue, setAnimationGoldValue] = useState(0);
  const [animationStyle, setAnimationStyle] = useState('');
  const shopkeeperCanvasRef = useRef(null);
  const animationRef = useRef(null);

  const [navBarHeight, setNavBarHeight] = useState(0);

  const [isShopkeeperHovered, setIsShopkeeperHovered] = useState(false);

  const DRAGGED_ITEM_X_OFFSET = 25;
  const DRAGGED_ITEM_Y_OFFSET = 80;

  useEffect(() => {
    getNavBarHeight();
  }, []);
  
  useEffect(() => {
    loadCurrencies();
    loadPlayerInventory();
    loadProductsData();
  }, [loadCurrencies, loadPlayerInventory, loadProductsData]);

  const prevInventoryGridRef = useRef(null);
  useEffect(() => {
    if (prevInventoryGridRef.current !== null && prevInventoryGridRef.current !== inventory_grid) {
      overlayItemsOnGrid();
    }
    prevInventoryGridRef.current = inventory_grid;
  }, [inventory_grid]);

  const prevBarHeightRef = useRef(null);
  useEffect(() => {
    if (prevBarHeightRef.current !== null && prevBarHeightRef.current !== navBarHeight) {
      overlayEquippedItems();
      overlayItemsOnGrid();
    }
    prevBarHeightRef.current = navBarHeight;
  }, [navBarHeight]); 

  const prevEquippedRef = useRef(null);
  useEffect(() => {
    if (prevEquippedRef.current !== null && prevEquippedRef.current !== equipped) {
      overlayEquippedItems();
    }
    prevEquippedRef.current = equipped;
  }, [equipped]); 

  useEffect(() => {
    const handleResize = () => {
      getNavBarHeight();
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  });


  useEffect(() => {
    if (shopState.gold_received > 0 || shopState.gold_spent > 0) {
      const goldChange = shopState.gold_received > 0 ? shopState.gold_received : -shopState.gold_spent;

      setAnimationGoldValue(goldChange);
      if (shopState.gold_spent > 0) {
        const canvasRect = shopkeeperCanvasRef.current.getBoundingClientRect();
        const animationRect = animationRef.current.getBoundingClientRect();

        setAnimationPosition({ 
          x: canvasRect.left + canvasRect.width / 2 - animationRect.width / 2, 
          y: canvasRect.top + canvasRect.height / 2 - animationRect.height / 2 - navBarHeight, 
        });
      } else {
        setAnimationPosition(draggedItemPosition);
      }

      if (shopState.gold_received > 0) {
        shopState.gold_received = 0;
        setAnimationStyle('sellAnimation');
      } else {
        shopState.gold_spent = 0;
        setAnimationStyle('buyAnimation');
      }

      setShowSellAnimation(true); // Activate animation
      setAnimationKey(prevKey => prevKey + 1);

      const animationTimer = setTimeout(() => {
        setShowSellAnimation(false); // Deactivate animation after 2 seconds
      }, 2000);

      return () => clearTimeout(animationTimer);
    }
  }, [shopState.gold_received, shopState.gold_spent]);



  const generateGridSquares = () => {
    const gridSquares = [];

    for (let rowIndex = 0; rowIndex < BAG_HEIGHT; rowIndex++) {
      for (let colIndex = 0; colIndex < BAG_WIDTH; colIndex++) {

        const squareId = `${colIndex}-${rowIndex}`;

        gridSquares.push(
          <GridSquare
            squareId={squareId}

            onDragOver={(e) => handleDragOverGrid(e, rowIndex, colIndex)}
            onDrop={(e) => handleDropOnGrid(e, rowIndex, colIndex)}
          />
        );
      }
    }

    return gridSquares;
  };

  const resetHighlightedSquares = () => {
    const gridSquares = document.querySelectorAll('[grid_square_pos]');

    setCanBeDroppedOnBag(false);

    gridSquares.forEach((square) => {
      square.classList.remove(`GridSquare_gridSquare_highlighted__pGfKC`);
    });
  };

  const handleDragOverGrid = (e, dropRow, dropCol) => {
    if (dropCol !== currentOccludedSquare.x || dropRow !== currentOccludedSquare.y || canBeDroppedOnBag === false) {
      setcurrentOccludedSquare({ x: dropCol, y: dropRow });
      
      if (draggedItemId !== 0) {
        e.preventDefault(); // Allow drop

        const squaresToHighlight = calculateHighlightedSquares(dropRow, dropCol, ALL_ITEMS_SIZES[items[draggedItemId]["size"]]);

        // If outside bounds, don't highlight and reset highlighted squares
        if (!squaresToHighlight) {
          resetHighlightedSquares();
        } else {
          setCanBeDroppedOnBag(true);
          const gridSquares = document.querySelectorAll('[grid_square_pos]');
          gridSquares.forEach((square) => {
            const squareId = square.getAttribute('grid_square_pos');
            square.classList.toggle('GridSquare_gridSquare_highlighted__pGfKC', squaresToHighlight.includes(squareId));
        });
        }
      }
    }
  };

  function calculateHighlightedSquares(dropRow, dropCol, itemSize) {
    const squaresToHighlight = [];
  
    for (let col = dropCol; col < dropCol + itemSize[0]; col++) {
      for (let row = dropRow; row < dropRow + itemSize[1]; row++) {
        if (col < 0 || col >= 8 || row < 0 || row >= 10 || (inventory_grid[row][col] !== null && inventory_grid[row][col] !== draggedItemId)) {
          return false; // Exit early if outside bounds or occupied square
        }
        squaresToHighlight.push(`${col}-${row}`);
      }
    }
  
    return squaresToHighlight;
  }

  const getNavBarHeight = () => {
    const navBar = document.querySelector('.navbar');
    const height = navBar ? navBar.clientHeight : 0;
    setNavBarHeight(height);
  };

  const overlayItemsOnGrid = () => {
    if (!inventory_grid || !items || gridContainerRef.current === null) {
      return null; // Return null or a loading indicator if data is not available yet
    }
  
    const gridRect = gridContainerRef.current.getBoundingClientRect();
    const renderedItems = new Set();

    setBagItems (
      <div>
        {inventory_grid.map((row, rowIndex) =>
          row.map((itemId, colIndex) => {
            if (itemId && !renderedItems.has(itemId)) {
              const item = items[itemId];

              // Mark this item as rendered
              renderedItems.add(itemId);

              let left = gridRect.left + colIndex * ITEM_GRID_WIDTH;
              let top  = gridRect.top  + rowIndex * ITEM_GRID_HEIGHT - navBarHeight;

              return (
                <Item
                  key={`item_${itemId}_${draggedItemPosition.x}_${draggedItemPosition.y}`}

                  item    = {item}
                  itemId  = {itemId}
                  startPosLeft = {left}
                  startPosTop  = {top}

                  listeners={{
                    onDragStart: handleDragStart,
                    onDragOver: resetHighlightedSquares,
                  }}
                />
              );
            }
            return null;
          })
        )}
      </div>
    );
  };
  

  useEffect(() => {
    if (!isCoursorOnBag) {
      resetHighlightedSquares();
    }
  }, [isCoursorOnBag]);

  const handleDragStart = (e, itemId) => {
    setDraggedItemId(itemId);
  };

  const handleDraggedImage = (e) => {
    e.preventDefault();

    setDraggedItemPosition({ x: e.clientX - DRAGGED_ITEM_X_OFFSET, y: e.clientY - DRAGGED_ITEM_Y_OFFSET});

    if (gridContainerRef.current && draggedItemId !== 0) {
      const gridRect = gridContainerRef.current.getBoundingClientRect();
      const isCursorInsideObject =
        draggedItemPosition.x + 25 >= gridRect.left  &&
        draggedItemPosition.x + 25 <= gridRect.right &&
        draggedItemPosition.y + 80 >= gridRect.top   &&
        draggedItemPosition.y + 80 <= gridRect.bottom;

      setIsCursorOnBag(isCursorInsideObject);
    }
  };

  const afterEveryDrop = (e) => {
    const targetElement = e.target;

    let foundGridSquare = false;
    let foundEquipCanvas = false;
    let foundShopkeeperCanvas = false;
    
    for (let className of targetElement.classList) {
      if (className.includes('gridSquare')) {
        foundGridSquare = true;
      }
      if (className.includes('equipCanvas') || className.includes('itemSlot')) {
        foundEquipCanvas = true;
      }
      if (className.includes('shopkeeperCanvas')) {
        foundShopkeeperCanvas = true;
      }
    }
    
    if (!foundGridSquare && !foundEquipCanvas && !foundShopkeeperCanvas) {
      overlayItemsOnGrid();
    } else if (foundGridSquare){
      if(!canBeDroppedOnBag){
        overlayItemsOnGrid();
      }
    }
  };

  const handleDropOnGrid = (e, rowIndex, colIndex) => {
    const itemId = e.dataTransfer.getData('itemId');

    if (itemId) {
      e.preventDefault();

      resetHighlightedSquares();

      if(canBeDroppedOnBag){
        moveItemInInventory(itemId, { col: colIndex, row: rowIndex })
      }
    }
  };

  const afterEquipDrop = (e) => {
    if (draggedItemId !== 0) {
      const equipCanvasRect    = document.querySelector(`.${styles.equipCanvas}`).getBoundingClientRect();
      const equipCanvasCenterX = equipCanvasRect.left + equipCanvasRect.width / 2;
      const mouseX             = e.clientX;

      if (mouseX < equipCanvasCenterX) {
        inventoryItemUse(draggedItemId, 'left')
      } else {
        inventoryItemUse(draggedItemId, 'right')
      }
    }
  };

  const afterShopDrop = (e) => {
    const itemId = e.dataTransfer.getData('itemId');

    if (draggedItemId !== 0) {
      sellPlayerItem(itemId)
    }
  };

  const equipmentSlots = [
    { slotName: 'weapon_left',  defaultPosLeft: 284 + 80 - 71,  defaultPosTop: 110 + 101 }, // Sum: 595 211
    { slotName: 'weapon_right', defaultPosLeft: 284 + 388 - 71, defaultPosTop: 110 + 101 }, // Sum: 903 211
    { slotName: 'helmet',       defaultPosLeft: 284 + 234 - 71, defaultPosTop: 110 + 21 },  // Sum: 749 131
    { slotName: 'body_armor',   defaultPosLeft: 284 + 234 - 71, defaultPosTop: 110 + 295 }, // Sum: 749 405
    { slotName: 'belt',         defaultPosLeft: 284 + 234 - 71, defaultPosTop: 110 + 537 }, // Sum: 749 647
    { slotName: 'gloves',       defaultPosLeft: 284 + 80 - 71,  defaultPosTop: 110 + 445 }, // Sum: 595 555
    { slotName: 'boots',        defaultPosLeft: 284 + 388 - 71, defaultPosTop: 110 + 445 }, // Sum: 903 555
    { slotName: 'amulet',       defaultPosLeft: 284 + 234 - 36, defaultPosTop: 110 + 193 }, // Sum: 784 303
    { slotName: 'ring_left',    defaultPosLeft: 284 + 80 - 36,  defaultPosTop: 110 + 343 }, // Sum: 630 453
    { slotName: 'ring_right',   defaultPosLeft: 284 + 388 - 36, defaultPosTop: 110 + 343 }, // Sum: 938 453
  ];

  const overlayEquippedItems = () => {
    if (typeof equipped !== 'undefined') {
      setEquippedItems (
        <div>
          {equipmentSlots.map((equipmentSlot) => {
            const { slotName, defaultPosLeft, defaultPosTop } = equipmentSlot;

            if (equipped[slotName] !== -1) {
              const equippedItemId = equipped ? equipped[slotName] : -1;
          
              const itemSlot = document.getElementById(slotName + '_slot');
              const rect = itemSlot?.getBoundingClientRect();
          
              const absoluteLeft = rect ? rect.left + window.scrollX + 1 : defaultPosLeft;
              const absoluteTop = rect ? rect.top + window.scrollY + 1 - navBarHeight : defaultPosTop;
  
              return (
                <Item
                  key={`item_${equippedItemId}`}
      
                  item={items[equippedItemId]}
                  itemId={equippedItemId}
      
                  startPosLeft={absoluteLeft} // You can adjust the initial position as needed
                  startPosTop={absoluteTop} // You can adjust the initial position as needed
      
                  listeners={{
                    onDragStart: handleDragStart
                  }}
      
                  equipState={true}
                />
              );
            }
            
          })}
        </div>
      );
    }
  };

  const handleMouseEnterShopkeeper = () => {
    setIsShopkeeperHovered(true);
  };

  const handleMouseLeaveShopkeeper = () => {
    setIsShopkeeperHovered(false);
  };

  const handlePurchase = (itemName) => {
    buyItem(itemName)
  };

  return (
    <div className={`${styles.containerBackground}`} onDragOver={(e) => handleDraggedImage(e)} onDrop={(e) => afterEveryDrop(e)} >
      <div className={`${styles.containerHeroView} mt-4`}>
        <div className={`${styles.containerHorizontal} ${styles.containerHorizontalTop}`}>

          <div className={`${styles.containerHeroName}`}>
            <h2>{nick_name_global}</h2>
          </div>

          <div className={`${styles.containerCurrency}`}>
            <div className={`${styles.currencyItem}`}>
              <img src={require('../../Images/HeroScreen/Gold.png')} alt="Gold Icon" className={styles.goldIcon} />
              <span className={`${styles.currencyAmount}`}>{currencies ? currencies.gold : 0}</span>
            </div>
            <div className={`${styles.currencyItem}`}>
              <img src={require('../../Images/HeroScreen/Diamond.png')} alt="Diamond Icon" className={styles.diamondIcon} />
              <span className={`${styles.currencyAmount}`}>{currencies ? currencies.diamonds : 0}</span>
            </div>
          </div>

          <div className={styles.containerShopkeeperName}>
            <h2>Gribblenog Bubblesnarl</h2>
            {/* <h2>Thalador Shimmerstone</h2> */}
          </div>

        </div>

        <div className={`${styles.containerHorizontal} ${styles.containerHorizontalBottom}`}>
          <div className={`${styles.equipCanvas}`} onDrop={(e) => afterEquipDrop(e)}>
            <div className={`${styles.slotsHolder}`}>
              <div id='weapon_left_slot'  className={`${styles.itemSlot} ${styles.bigItemSlot}    ${styles.weaponLeft}`}  ></div>
              <div id='weapon_right_slot' className={`${styles.itemSlot} ${styles.bigItemSlot}    ${styles.weaponRight}`} ></div>
              <div id='helmet_slot'       className={`${styles.itemSlot} ${styles.mediumItemSlot} ${styles.helmet}`}      ></div>
              <div id='body_armor_slot'   className={`${styles.itemSlot} ${styles.bigItemSlot}    ${styles.armor}`}       ></div>
              <div id='belt_slot'         className={`${styles.itemSlot} ${styles.mediumItemSlot} ${styles.belt}`}        ></div>
              <div id='gloves_slot'       className={`${styles.itemSlot} ${styles.mediumItemSlot} ${styles.gloves}`}      ></div>
              <div id='boots_slot'        className={`${styles.itemSlot} ${styles.mediumItemSlot} ${styles.boots}`}       ></div>
              <div id='amulet_slot'       className={`${styles.itemSlot} ${styles.smallItemSlot}  ${styles.amulet}`}      ></div>
              <div id='ring_left_slot'    className={`${styles.itemSlot} ${styles.smallItemSlot}  ${styles.ringLeft}`}    ></div>
              <div id='ring_right_slot'   className={`${styles.itemSlot} ${styles.smallItemSlot}  ${styles.ringRight}`}   ></div>
            </div>

            {equippedItems}     
          </div>
          
          <div className={`${styles.bagCanvas}`}>
            <div className={`${styles.gridContainer}`}
            ref={gridContainerRef}
            >
              {generateGridSquares()}
            </div>
            {bagItems}
          </div>


          <div className={`${styles.shopkeeperCanvas}`} onDrop={(e) => afterShopDrop(e)}>
            <div 
              className={styles.shopkeeperCanvasImage}
              onMouseEnter={handleMouseEnterShopkeeper} 
              onMouseLeave={handleMouseLeaveShopkeeper}
              ref={shopkeeperCanvasRef}
            >
              {showSellAnimation && (
                <div
                  key={animationKey}
                  className={styles[animationStyle]}
                  style={{ left: animationPosition.x, top: animationPosition.y }}
                  ref={animationRef}
                >
                  {animationGoldValue > 0 ? `+${animationGoldValue}` : animationGoldValue} <img src={require('../../Images/Shop/CoinSmall.png')} alt="Coin" />
                  {/* {`+${animationGoldValue}`} <img src={require('../../Images/Shop/CoinSmall.png')} alt="Coin" /> */}
                </div>
              )}
            </div>

            {isShopkeeperHovered ? (
              <div className={styles.shopkeeperCanvasText}>
                {/* <h3>Welcome, traveler! Step into the treasury of brilliance and mystery. I am Thalador Shimmerstone, your guide through the realms of the enchanted marketplace. What brings you here today?</h3> */}
                <h3>Ah-ha! Welcome! Im Gribblenog Bubblesnarl, your zany alchemist. My potions might bubble, fizzle, or even explode  just how you like it! What can I brew up for you today?</h3>
                <h2>Buys for 50% the cost.</h2>
              </div>
            ) : (
              <div className={styles.potionPanel}>
                {shopProducts && shopProducts.length > 0 ? (
                  shopProducts.map(product => (
                    <div key={product.name} className={styles.potion}>
                      <ItemShop item={product}/>
                      <button onClick={() => handlePurchase(product.name)}>
                        {product.price} <img src={require('../../Images/Shop/CoinSmall.png')} alt="Coin" className={styles.coinIcon} />
                      </button>
                    </div>
                  ))
                ) : (
                  <p>No products available</p>
                )}
            </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = state => ({
  nick_name_global: state.profile.nick_name,
  currencies: state.currencies,
  shopState: state.shop,
  shopProducts: state.shop.products_data,

  items: state.inventory.items,
  inventory_grid: state.inventory.inventory_grid,
  equipped: state.inventory.equipped,
});

export default connect(mapStateToProps, { loadCurrencies, loadPlayerInventory, moveItemInInventory, inventoryItemUse, sellPlayerItem, loadProductsData, buyItem })(Shop);