// soundMiddleware.js
import { PLAYER_ITEM_SOLD_SUCCESS, BUY_ITEM_SUCCESS, USE_CONSUMABLE_ITEM_SUCCESS, USE_GEAR_ITEM_SUCCESS, MOVE_ITEM_SUCCESS } from '../actions/types';
import soundManager from '../audio/soundManager';

const soundMiddleware = store => next => action => {
  switch (action.type) {
    case PLAYER_ITEM_SOLD_SUCCESS:
        soundManager.playSound('coins');
        break;
    case BUY_ITEM_SUCCESS:
        soundManager.playSound('coins');
        break;
    case USE_CONSUMABLE_ITEM_SUCCESS:
        soundManager.playSound('eat');
        break;
    case USE_GEAR_ITEM_SUCCESS:
        soundManager.playSound('equip');
        break;
    case MOVE_ITEM_SUCCESS:
        soundManager.playSound('equip');
        break;
    default:
      break;
  }

  return next(action);
};

export default soundMiddleware;
