import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';

import Layout from './hocs/Layout';

import Home from './containers/containersJS/Home';
import Register from './containers/containersJS/Register';
import Login from './containers/containersJS/Login';
import Dashboard from './containers/containersJS/Dashboard';
import Hero from './containers/containersJS/Hero';
import Shop from './containers/containersJS/Shop';
import MonstersHunt from './containers/containersJS/MonstersHunt';
import Fight from './containers/containersJS/Fight';

import PrivateRoute from './hocs/PrivateRoute';

import { Provider } from 'react-redux';
import store from './store';

import BackgroundMusic from './audio/BackgroundMusic'; // Import the BackgroundMusic component
<BackgroundMusic />

const App = () => (
    <Provider store = {store}>
        <Router>
            <BackgroundMusic />
            <Layout>
                <Routes>
                    <Route path='/' element={<Home />} />
                    <Route path='/register' element={<Register />} />
                    <Route path='/login' element={<Login />} />
                    <Route path='/hero' element={<Hero />} />
                    <Route path='/shop' element={<Shop />} />
                    <Route path='/monsters_hunt' element={<MonstersHunt />} />
                    <Route path='/fight' element={<Fight />} />
                    <Route element={<PrivateRoute />} >
                        <Route element={<Dashboard />} path='/dashboard'  />
                    </Route>
                </Routes>
            </Layout>
        </Router>
    </Provider>

);

export default App;