import Cookies from 'js-cookie';
import axios from 'axios';
import { clearError } from './errorUtils';

import {
    LOAD_USER_PROFILE_SUCCESS,
    LOAD_USER_PROFILE_FAIL,
    UPDATE_USER_PROFILE_SUCCESS,
    UPDATE_USER_PROFILE_FAIL,
    CLEAR_PROFILE_UPDATE_STATE
} from './types';

const updateProfileErrorMessages = [
    "Profile wasn't updated. \nMaybe the update fairy is on vacation today.",
    "Profile wasn't updated. \nIt seems like the server is on vacation.",
    "Profile wasn't updated. \nMaybe try refreshing the page and updating again.",
    "Profile wasn't updated. \nDid a squirrel interrupt the update process?",
    "Profile wasn't updated. \nIt's a mystery why your changes didn't go through."
];

const updateProfileSuccessMessages = [
    "Profile was updated. \nThe elves did a little dance to make it happen.",
    "Profile was updated. \nA friendly ghost whispered the changes.",
    "Profile was updated. \nThe update fairy sprinkled some magic dust.",
    "Profile was updated. \nA sneaky raccoon approved the changes.",
    "Profile was updated. \nThe profile gnomes worked their magic."
];

export const clearUpdateState = () => (dispatch) => {
    clearError(dispatch, CLEAR_PROFILE_UPDATE_STATE);
};

export const load_user = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/profile/user`, config);

        if (res.data.error) {
            dispatch({
                type: LOAD_USER_PROFILE_FAIL
            });
        } else {
            dispatch({
                type: LOAD_USER_PROFILE_SUCCESS,
                payload: res.data
            });
        }
    } catch (err) {
        dispatch({
            type: LOAD_USER_PROFILE_FAIL
        });
    }
};

export const update_profile = (nick_name, email) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken')
        }
    };

    const body = JSON.stringify({
        'withCredentials': true,
        nick_name,
        email
    });

    try {
        const res = await axios.put(`${process.env.REACT_APP_API_URL}/profile/update`, body, config);

        if (res.data.profile && res.data.username) {
            
            const randomUpdateSuccessErrorMessage = updateProfileSuccessMessages[Math.floor(Math.random() * updateProfileSuccessMessages.length)];
            dispatch({
                type: UPDATE_USER_PROFILE_SUCCESS,
                payload: {
                    ...res.data,
                    successMessage: randomUpdateSuccessErrorMessage
                }
            });
        } else {
            const randomUpdateFailErrorMessage = updateProfileErrorMessages[Math.floor(Math.random() * updateProfileErrorMessages.length)];
            
            dispatch({
                type: UPDATE_USER_PROFILE_FAIL,
                payload: randomUpdateFailErrorMessage
            });
        }
    } catch (err) {
        dispatch({
            type: UPDATE_USER_PROFILE_FAIL
        });
    }
};