import axios from 'axios';
import {
    LOAD_HERO_CURRENCIES_SUCCESS,
    LOAD_HERO_CURRENCIES_FAIL
} from './types';

export const loadCurrencies = (currencies = null) => async dispatch => {
    if (currencies) {
        // Jeli przekazano dane walut jako argument, po prostu je zaktualizuj w reduktorze
        dispatch({
            type: LOAD_HERO_CURRENCIES_SUCCESS,
            payload: currencies
        });
    } else {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        };

        try {
            const res = await axios.get(`${process.env.REACT_APP_API_URL}/currencies/get_user_currencies`, config);

            if (res.data.currencies) {
                dispatch({
                    type: LOAD_HERO_CURRENCIES_SUCCESS,
                    payload: res.data.currencies
                });
            } else {
                dispatch({
                    type: LOAD_HERO_CURRENCIES_FAIL
                });
            }
        } catch (err) {
            dispatch({
                type: LOAD_HERO_CURRENCIES_FAIL
            });
        }
    }
};
