import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { login, clearLoginError } from '../../actions/auth'; // Import the clearError action
import CSRFToken from '../../components/CSRFToken';
import styles from '../containersCSS/Login.module.css'; // Import the CSS file

const Login = ({ login, isAuthenticated, error, clearLoginError }) => {
    const [formData, setFormData] = useState({
        username: '',
        password: ''
    });

    const { username, password } = formData;

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        login(username, password);
    };

    useEffect(() => {
        clearLoginError();
    }, [clearLoginError]);

    if (isAuthenticated)
        return <Navigate to='/dashboard' />;

    return (
        <div className={`${styles.containerBackground}`}>
            <div className={`${styles.container} mt-4`}>
                <div className={`${styles.loginContainer}`}>
                    <h1>Sign In</h1>
                    <p>Sign in and destroy all enemies!</p>
                
                    {error && <p className={`${styles.errorMessageContainer} mt-2`}>{error}</p>}

                    <form onSubmit={e => onSubmit(e)}>
                        <CSRFToken />
                        <div className='form-group mt-2 '>
                            <label className='form-label'>Username: </label>
                            <input
                                className='form-control'
                                type='text'
                                placeholder='Username*'
                                name='username'
                                onChange={e => onChange(e)}
                                value={username}
                                required
                            />
                        </div>
                        <div className='form-group'>
                            <label className='form-label mt-3'>Password: </label>
                            <input
                                className='form-control'
                                type='password'
                                placeholder='Password*'
                                name='password'
                                onChange={e => onChange(e)}
                                value={password}
                                minLength='6'
                                required
                            />
                        </div>

                        <div className='text-center mt-3'>
                            <button className='btn btn-primary mt-2' type='submit'>Login</button>
                        </div>

                    </form>

                    <p className='mt-3'>
                        Don't have an Account? <Link to='/register'>Sign Up</Link>
                    </p>
                </div>
            </div>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    error: state.auth.error
});

export default connect(mapStateToProps, { login, clearLoginError })(Login);
