import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { update_profile, clearUpdateState } from '../../actions/profile';
import { delete_account } from '../../actions/auth';
import styles from '../containersCSS/Dashboard.module.css'; // Import the CSS file

const Dashboard = ({
    delete_account,
    update_profile,
    nick_name_global,
    email_global,
    error,
    successMessage,
    clearUpdateState
}) => {
    const [formData, setFormData] = useState({
        nick_name: '',
        email: '',
    });

    const { nick_name, email } = formData;

    useEffect(() => {
        clearUpdateState();
    }, [clearUpdateState]);

    useEffect(() => {
        setFormData({
            nick_name: nick_name_global,
            email: email_global,
        });
    }, [nick_name_global, email_global]);

    const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();
    
        if (email.trim() !== '' && !email.includes('@')) {
            console.error('Email is invalid (lack of "@"):', email);
        } else {
            update_profile(nick_name, email);
        }
    };

    return (
        <div className={`${styles.containerBackground}`}>
            <div className={`${styles.container} mt-4`}>
                <div className={`${styles.dashboardContainer}`}>
                    <h1 className='mt-3'>
                        Welcome to your User Dashboard
                    </h1>

                    <p className='mt-2 mb-3'>
                        Update your user profile below:
                    </p>

                    {error && <p className={`${styles.errorMessageContainer} mt-2`}>{error}</p>}
                    {successMessage && <p className={`${styles.successMessageContainer} mt-2`}>{successMessage}</p>}

                    <form onSubmit={e => onSubmit(e)}>
                        <div className='form-group'>
                            <label className='form-label mt-2' htmlFor='nick_name'>Nick Name</label>
                            <input
                                className='form-control'
                                type='text'
                                name='nick_name'
                                placeholder={`${nick_name_global}`}
                                onChange={e => onChange(e)}
                                value={nick_name}
                            />
                        </div>
                        <div className='form-group'>
                            <label className='form-label mt-3' htmlFor='email'>Email</label>
                            <input
                                className='form-control'
                                type='text'
                                name='email'
                                placeholder={`${email_global}`}
                                onChange={e => onChange(e)}
                                value={email}
                            />
                        </div>
                        {email && !email.includes('@') && (
                            <div className="text-danger">Email must contain '@' symbol</div>
                        )}

                        <div className={`${styles.centerButton} mt-4`}>
                            <button className='btn btn-primary' type='submit'>Update Profile</button>
                        </div>
                    </form>

                    <hr className='my-4' />
                    
                    <p className='mt-4'>
                        Click the button below to delete your user account:
                    </p>
                    
                    <div className={`${styles.centerButton}`}>
                        <a className='btn btn-danger'
                            href='#!'
                            onClick={delete_account}
                        >
                            Delete Account
                        </a>
                    </div>
                </div>
            </div>
        </div>
    )
};

const mapStateToProps = state => ({
    nick_name_global: state.profile.nick_name,
    email_global: state.profile.email,
    error: state.profile.error,
    successMessage: state.profile.successMessage,
});

export default connect(mapStateToProps, { 
    delete_account,
    update_profile,
    clearUpdateState
 })(Dashboard);