import React from 'react';
import styles from './GridSquare.module.css'; // Import your CSS styles if needed

const GridSquare = ({ squareId, onDragOver, onDrop }) => {
    return (
        <div
            className={styles.gridSquare} // Use your CSS class here

            key={squareId}
            grid_square_pos={squareId}

            onDragOver={onDragOver}
            onDrop={onDrop}
        ></div>
    );
};

export default GridSquare;