import React, { useEffect } from 'react';
import styles from '../containersCSS/MonstersHunt.module.css'; // Import the CSS file
import { connect } from 'react-redux';
import { start_fight, get_hunt_levels_unlocked } from '../../actions/fight';

const MonstersHuntPage = ({ start_fight, huntLevelsUnlocked, get_hunt_levels_unlocked }) => {
  useEffect(() => {
    get_hunt_levels_unlocked();
  }, [get_hunt_levels_unlocked]);

  const handleFightButtonClick = (monster_lvl) => {
    if (monster_lvl <= huntLevelsUnlocked) {
      start_fight(monster_lvl);
    }
  };

  return (
    <div className={`${styles.containerBackground}`}>
      <div className={`${styles.container} mt-4`}>
        <div className={`${styles.contentContainer}`}>
          <h1>Monsters Hunt!</h1>
          <p>Ready to hunt some beasts? Join the adventure and show them who is the superior species!</p>
          
          <div className={`${styles.worldContainer}`}>
            <h1>Nearby lands</h1>
            <div className={`${styles.buttonContainer}`}>
              {[...Array(10).keys()].map(i => {
                const monster_lvl = i + 1;
                return (
                  <button
                    key={monster_lvl}
                    className={`${styles.button} ${monster_lvl > huntLevelsUnlocked ? styles.locked : ''}`}
                    onClick={() => handleFightButtonClick(monster_lvl)}
                    disabled={monster_lvl > huntLevelsUnlocked}
                  >
                    Lvl {monster_lvl} - {getLevelName(monster_lvl)}
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const getLevelName = (lvl) => {
  const names = [
    'Buttercup Meadow',
    'Poppysnuggle Fields',
    'Cherryblossom Grove',
    'Honeydew Glade',
    'Trillingshade Woods',
    'Whispering Peaks',
    'Enchanted Vale',
    'Emerald Garden',
    'Lullaby Clearing',
    'Pixie Hollow'
  ];
  return names[lvl - 1];
};

const mapStateToProps = (state) => ({
  huntLevelsUnlocked: state.fight.huntLevelsUnlocked
});

export default connect(mapStateToProps, { start_fight, get_hunt_levels_unlocked })(MonstersHuntPage);
