import React, { useEffect, Fragment } from 'react';
import Navbar from "../components/Navbar";
import { connect } from 'react-redux';
import { Navigate, useLocation } from 'react-router-dom';

import { checkAuthenticated } from '../actions/auth';
import { load_user } from '../actions/profile';
import { get_fight_status } from '../actions/fight';

const Layout = ({
  children,
  checkAuthenticated,
  load_user,
  isAuthenticated,
  inFight,
  get_fight_status,
}) => {
    useEffect(() => {
        checkAuthenticated()
    }, [checkAuthenticated]);

    useEffect(() => {
        if (isAuthenticated) {
            load_user();
            get_fight_status();
        }
    }, [isAuthenticated, load_user, get_fight_status]);

    const location = useLocation(); // Use the useLocation hook
    const isFightScreen = location.pathname === '/fight';

    if (inFight !== null) {
        if (inFight && isAuthenticated && isFightScreen === false) {
            // console.log("GO TO FIGHT PAGE ", inFight, isAuthenticated, isFightScreen);
            return <Navigate to="/fight" />;
        } else if (!inFight && isAuthenticated && isFightScreen === true) {
            // console.log("GO TO MONSTER HUNT PAGE", inFight, isAuthenticated, isFightScreen);
            return <Navigate to="/monsters_hunt" />;
        }
    }

    return (
        <Fragment>
            <Navbar />
            {children}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
    inFight: state.fight.inFight,
});

export default connect(mapStateToProps, { checkAuthenticated, load_user, get_fight_status })(Layout);