import { configureStore } from '@reduxjs/toolkit';
import { createLogger } from 'redux-logger';
import thunk from 'redux-thunk';
import rootReducer from './reducers';
import soundMiddleware from './middleware/soundMiddleware';

const loggerMiddleware = createLogger();

const store = configureStore({
    reducer: rootReducer,
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(thunk, soundMiddleware),
    // middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(thunk, soundMiddleware, loggerMiddleware),
});

export default store;
