import axios from 'axios';
import Cookies from 'js-cookie';

import {
    LOAD_PLAYER_INVENTORY_SUCCESS,
    LOAD_PLAYER_INVENTORY_FAIL,
    MOVE_ITEM_SUCCESS,
    MOVE_ITEM_FAIL,
    USE_ITEM_SUCCESS,
    USE_ITEM_FAIL,
    USE_CONSUMABLE_ITEM_SUCCESS,
    USE_GEAR_ITEM_SUCCESS,
} from './types';

export const loadPlayerInventory = () => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    };

    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/inventory/get`, config);

        if (res.data.player_inventory) {
            dispatch({
                type: LOAD_PLAYER_INVENTORY_SUCCESS,
                payload: res.data
            });
        } else {
            dispatch({
                type: LOAD_PLAYER_INVENTORY_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: LOAD_PLAYER_INVENTORY_FAIL
        });
    }
};

// Action to move an item in the inventory
export const moveItemInInventory = (item_id, new_position) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };

    const body = JSON.stringify({ item_id, new_position });

    try {
        // Make a POST request to move the item
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/inventory/move_in_bag`, body, config);

        if (res.data.player_inventory) {
            dispatch({
                type: MOVE_ITEM_SUCCESS,
                payload: res.data
            });

        } else {
            dispatch({
                type: MOVE_ITEM_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: MOVE_ITEM_FAIL
        });
    }
};

export const inventoryItemUse = (item_id, drop_side) => async dispatch => {
    const config = {
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'X-CSRFToken': Cookies.get('csrftoken'),
        }
    };

    const body = JSON.stringify({ item_id, drop_side });

    try {
        const res = await axios.post(`${process.env.REACT_APP_API_URL}/inventory/use_item`, body, config);

        if (res.data.player_inventory && res.data.item_used === 'consumable' ) {
            dispatch({
                type: USE_CONSUMABLE_ITEM_SUCCESS,
            });
        } else if (res.data.player_inventory && res.data.item_used === 'gear' ) {
            dispatch({
                type: USE_GEAR_ITEM_SUCCESS
            });
        }

        if (res.data.player_inventory) {
            dispatch({
                type: USE_ITEM_SUCCESS,
                payload: res.data
            });
        } else {
            dispatch({
                type: USE_ITEM_FAIL
            });
        }
    } catch (err) {
        dispatch({
            type: USE_ITEM_FAIL
        });
    }
};