import {
    FIGHT_START_SUCCESS,
    FIGHT_START_FAILURE,
    
    FIGHT_STATUS_TRUE,
    FIGHT_STATUS_FALSE,
    FIGHT_STATUS_FAILURE,
    
    RETREAT_STATUS_TRUE,
    RETREAT_STATUS_FALSE,

    LEAVE_STATUS_TRUE,
    LEAVE_STATUS_FALSE,

    FIGHT_DATA_SUCCESS,
    FIGHT_DATA_FAILURE,
    FIGHT_DATA_NOT_FOUND,

    GET_HUNT_LEVELS_SUCCESS,
    GET_HUNT_LEVELS_FAILURE,
} from '../actions/types';
  
const initialState = {
    huntLevelsUnlocked: 1,
    inFight: null,
    fightIsOver: true,
    message: '',
};
  
export default function fightReducer(state = initialState, action) {
    const { type, payload } = action;

    // console.log('FIGHT_DATA_SUCCESS Payload: ', type, "     ",  payload, "   ", state);
    switch (type) {
        case FIGHT_START_SUCCESS:
            return {
                ...state,
                inFight: true,
                fightIsOver: false,
                message: payload.message,
            };
        case FIGHT_START_FAILURE:
            return {
                ...state,
                message: payload,
            };        
        case RETREAT_STATUS_TRUE:
            return {
                ...state,
                fightIsOver: true,
                message: payload,
            };        
        case RETREAT_STATUS_FALSE:
            return {
                ...state,
                message: payload,
            };
        case LEAVE_STATUS_TRUE:
            return {
                ...state,
                inFight: false,
                message: payload,
            };        
        case LEAVE_STATUS_FALSE:
            return {
                ...state,
                message: payload,
            };
        case FIGHT_STATUS_TRUE:
            return {
                ...state,
                inFight: true,
                message: '', // Clear any previous error message
            };
        case FIGHT_STATUS_FALSE:
            return {
                ...state,
                inFight: false,
                message: '', // Clear any previous error message
            };
        case FIGHT_STATUS_FAILURE:
            return {
                ...state,
                message: payload,
            };
        case FIGHT_DATA_SUCCESS:
            return {
                ...state,
                fightData: payload, // Set fightData in the state
            };
        case FIGHT_DATA_FAILURE:
            return {
                ...state,
                message: payload,
            };
        case FIGHT_DATA_NOT_FOUND:
            return {
                ...state,
                message: payload,
            };
        case GET_HUNT_LEVELS_SUCCESS:
            return {
                ...state,
                huntLevelsUnlocked: action.payload,
                loading: false,
                error: null,
            };
        case GET_HUNT_LEVELS_FAILURE:
            return {
                ...state,
                error: action.payload,
                loading: false,
            };
        default:
            return state;
    }
}